.FingoBackToTop {
    width: 42px;
    height: 50px;
    outline: none;
    background-color: rgb(0 205 156);
    border: none;
    padding: 5px;
    border-radius: 10px;
    box-shadow: rgb(0, 148, 113) 0px 6px;
    transition: all 0.2s ease 0s;
    font-weight: 100;
    position: absolute;
    position: fixed;
    bottom: 92px;
    right: 32px;
    z-index: 1000;
}

@media (min-width: 576px) {
    .FingoBackToTop {
        bottom: 24px;
    }
}
